.user_transactions_header_main {
  /* background-color: red; */
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: center;
      align-items: center;
  border: 1px solid #d5d5d5;
  /* padding: 5px 14px */
  border-radius: 10px;
}

.user_transactions_header_column {
  border-right: 1px solid #d5d5d5;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.user_transactions_header_column2 {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.user_transactions_header_heading {
  font-size: 14px;
  font-weight: bold;
}

#cars {
  border: none;
  outline: none;
  width: 100%;
  padding-right: 50px;
  font-size: 14px;
  font-weight: bold;
}
.reset_heading {
  font-size: 14px;
  /* font-weight: bold; */
  color: #ea0234;
  padding-left: 10px;
}
.table_back_btn_main{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-item-align: end;
        align-self: flex-end;
    -ms-flex-pack: end;
        justify-content: flex-end;
}
.left_btn{
    border: none;
    outline: none;
    border: 1px solid #D5D5D5;
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    cursor: pointer;
    padding:5px 10px;
}
.right_btn{
    border: none;
    outline: none;
    border: 1px solid #D5D5D5;
    cursor: pointer;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    padding:5px 10px;
}
@media only screen and (max-width: 600px) {
  #cars {
    border: none;
    outline: none;
    width: 100%;
    padding-right: 10px;
    font-size: 14px;
    font-weight: bold;
  }
  .user_transactions_header_column {
    border-right: 0px solid #d5d5d5;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
  }
}



